.page-header {
  min-height: 120px;
  position: fixed;
  width: 100%;
  z-index: 4;
  background: linear-gradient(to bottom, rgba(4, 24, 39, 1) 0%, rgba(4, 24, 39, 1) 91%, rgba(4, 24, 39, 0) 100%);

  @media (max-width: $desktop-width) {
    min-height: 100px;
  }

  @media (max-width: $tablet-width) {
    min-height: 70px;
    position: static;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  flex-wrap: wrap;

  @media (max-width: $desktop-width) {
    padding-top: 21px;
  }

  @media (max-width: $tablet-width) {
    padding-top: 16px;
  }
}

.page-header__contacts {
  display: flex;
  justify-content: space-between;
  padding-top: 6px;
  margin-right: 3px;

  @media (max-width: $desktop-width) {
    margin-right: 28px;
    padding-top: 0;
  }

  @media (max-width: $tablet-width) {
    margin-right: 20px;
  }

  & a {
    color: $font-light-grey;
    line-height: 18px;
    fill: $blue;
    display: block;
    position: relative;
    padding-left: 40px;
    padding-top: 9px;
    min-width: 225px;

    @media (max-width: $desktop-width) {
      min-width: 30px;
      padding-left: 31px;
    }

    @media (max-width: $tablet-width) {
      margin-top: -6px;
      padding-left: 0;
    }
  }

  & svg {
    position: absolute;
    left: 0;
    top: 0;
  }
}

.page-header__button {
  min-width: 160px;
  border: 1px solid $font-light-grey;
  box-sizing: border-box;
  border-radius: 3px;
  color: $font-light-grey;
  margin-top: 6px;
  margin-right: 58px;
  min-height: 40px;
  height: 100%;

  @media (max-width: $desktop-width) {
    display: none;
  }
}

.page-header__phone {
  margin-left: 32px;

  @media (max-width: $desktop-width) {
    & span {
      display: none;
    }
  }
}

.page-header__mail span {
  @media (max-width: $desktop-width) {
    display: none;
  }
}

.page-header__logo {
  display: block;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.page-header__logo-small {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
  }
}
