.about {
  margin-bottom: 120px;

  @media (max-width: $desktop-width) {
    margin-bottom: 79px;
  }

  & h2 {
    font-family: "Roboto Condensed", "Arial", monospace;
    font-size: 36px;
    line-height: 51px;
    color: $main-blue;
    margin: 0;

    @media (max-width: $tablet-width) {
      font-size: 28px;
      line-height: 39px;
      font-weight: 400;
    }
  }

  & p {
    font-size: 16px;
    line-height: 22px;
    color: $main-blue;

    @media (max-width: $desktop-width) {
      margin-top: 11px;
      margin-bottom: 19px;
    }

    @media (max-width: $tablet-width) {
      font-size: 14px;
      line-height: 19px;
      margin-bottom: 18px;
    }
  }
}

.about__wrap {
  &.show {
    .about__hidden {
      display: block;
    }

    .about__show-less {
      display: block;
    }

    .about__show-more {
      display: none;
    }
  }
}

.about__wrapper {
  padding-left: 250px;
  position: relative;

  @media (max-width: $desktop-width) {
    padding-left: 175px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 0;
  }

  &::before {
    content: " ";
    position: absolute;
    display: block;
    background-image: url("../img/sprite/2022.svg");
    width: 150px;
    height: 320px;
    left: 0;

    @media (max-width: $desktop-width) {
      left: -3px;
      top: 3px;
    }

    @media (max-width: $tablet-width) {
      display: none;
    }
  }

  & span {
    @media (max-width: $tablet-width) {
      display: none;
    }
  }
}

.about__hidden {
  padding-left: 250px;
  display: none;

  @media (max-width: $desktop-width) {
    padding-left: 175px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 0;
  }
}

.about__show-less {
  display: none;
}

.about__button {
  color: $blue;
  border: 1px solid $blue;
  box-sizing: border-box;
  border-radius: 3px;
  padding: 14px 54px 13px 45px;
  margin-left: 250px;
  margin-top: 23px;

  @media (max-width: $desktop-width) {
    margin-left: 175px;
    margin-top: -3px;
    letter-spacing: 0.6px;
    padding: 14px 51px 13px 49px;
  }

  @media (max-width: $tablet-width) {
    margin-left: 0;
    padding: 7px 40px 9px 40px;
  }
}
